'use strict';

import React from 'react';

import { Link } from 'react-router-dom';

class Islogin extends React.Component{
	render(){
		return(
			<section>
				<p></p>
				<p></p>
				<span><Link to="/my/login"></Link>/<Link to="/my/login"></Link></span>
			</section>
			)
	}
}

export default Islogin


